
function crop (imageBitmap, x, y, width, height, maxWidth, maxHeight) {
  console.log(`x: ${x}, y: ${y}, width: ${width}, height: ${height}`)

  let w = width
  let h = height
  if (width > height) {
    w = maxWidth
    h = height / width * maxWidth
  } else {
    h = maxHeight
    w = width / height * maxHeight
  }

  const offscreen = new OffscreenCanvas(w, h)
  const ctx = offscreen.getContext('2d')
  ctx.clearRect(0, 0, offscreen.width, offscreen.height)
  ctx.drawImage(imageBitmap, x, y, width, height, 0, 0, w, h)

  const blob = offscreen.convertToBlob({type: 'image/png'})
  const bitmap = offscreen.transferToImageBitmap()

  return {blob: blob, bitmap: bitmap}
}

function blobToUrl (blob) {
  return new Promise((resolve, reject) => {
    var fr = new FileReader()
    fr.onload = () => {
      resolve(fr.result)
    }
    fr.onerror = reject
    fr.readAsDataURL(blob)
  })
}

export default {
  crop,
  blobToUrl
}



