<template>
  <v-ons-page>
    <video v-show="!isLoading" class="camera-surface" ref="camera" autoplay></video>
    <canvas ref="pixiCanvas"></canvas>
    <lottie-view :class="['okBtn', isPhotoTaken ? 'fadein' : 'fadeout']"
      ref="btn" width="100px" height="100px"
      :speed="2" :loop=true :autoPlay="false"
      path="static/dot-loading.json"
      @click="onClick" @complete="onComplete" />
  </v-ons-page>
</template>

<script>
import * as PIXI from 'pixi.js'
import Resizable from '../lib/resizable.js'
import LottieView from './LottieView.vue'
import ImageX from '../lib/imgExt.js'
import axios from 'axios'
import EditPage from './EditPage.vue'

export default {
  name: 'CanvasPage',
  data () {
    return {
      app: null,
      isLoading: true,
      isPhotoTaken: false,
      imageCapture: null
    }
  },
  mounted () {
    navigator.mediaDevices.getUserMedia({audio: false, video: { facingMode: 'environment', width: screen.width, height: screen.height }}).then(stream => {
      this.isLoading = false
      this.$refs.camera.srcObject = stream
      const track = stream.getVideoTracks()[0]
      this.imageCapture = new window.ImageCapture(track)
    }).catch(error => {
      this.isLoading = false
      console.error('Platform Webview does not support getUserMedia video.')
      console.error(error)
    })

    this.app = new PIXI.Application({
      backgroundAlpha: 0,
      antialias: true,
      view: this.$refs.pixiCanvas
    })
    // this.$el.appendChild(this.app.view)
    this.app.renderer.view.className = 'canvas-surface'
    this.app.renderer.view.style.position = 'fixed'
    this.app.renderer.view.style.width = '100%'
    this.app.renderer.view.style.height = '100%'
    this.app.renderer.view.style.zIndex = '10'
    this.app.renderer.autoResize = true
    this.app.renderer.resize(window.innerWidth, window.innerHeight)

    Resizable.init(this.app)
  },
  methods: {
    progressStart () {
      this.$refs.btn.anim.show()
      this.$refs.btn.anim.goToAndPlay(0)
    },
    progressEnd () {
      this.$refs.btn.anim.hide()
      this.$refs.btn.anim.goToAndStop(0)
    },
    onClick () {
      console.log('onClick')
      this.isPhotoTaken = !this.isPhotoTaken
      if (this.isPhotoTaken) {
        this.progressStart()
        this.takePhoto()
      } else {
        this.progressEnd()
      }
    },
    goEdit () {
      this.$emit('replace-page', EditPage)
    },
    onComplete () {
      console.log('onComplete')
      this.$refs.btn.anim.stop()
      this.isPhotoTaken = !this.isPhotoTaken
      this.takePhoto()
    },
    takePhoto () {
      if (this.isLoading) {
        console.log('camera is not ready')
        return
      }

      this.imageCapture.takePhoto()
        .then(blob => {
          console.log(blob)
          return createImageBitmap(blob)
        })
        .then(bitmap => {
          const size = Resizable.size()
          const ratio = screen.orientation.angle / 90 % 2 === 1 ? bitmap.width / this.app.view.width : bitmap.height / this.app.video.height
          const w = size.width * ratio
          const h = size.height * ratio
          const img = ImageX.crop(bitmap, Math.max(0, (bitmap.width - w) / 2), Math.max(0, (bitmap.height - h) / 2), w, h, 300, 300)
          return img.blob
        }).then(blob => {
          ImageX.blobToUrl(blob).then(url => localStorage.setItem('raster', url))
          let data = new FormData()
          data.append('hierarchial', 'cutout')
          data.append('filter_speckle', 4)
          data.append('color_precision', 6)
          data.append('layer_difference', 16)
          data.append('file', blob)
          return axios.post('https://fierce-lake-44609.herokuapp.com/vision/', data, {header: {'Content-Type': 'multipart/form-data'}})
        }).then(response => {
          localStorage.setItem('vectorized', response.data)
          this.progressEnd()
          this.goEdit()
        }).catch(error => {
          console.log('error...')
          console.log(error)
          this.progressEnd()
        })
    }
  },
  components: {
    LottieView,
    EditPage
  }
}
</script>

<style scoped>
.camera-surface {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.stage > canvas {
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 10;
}

.okBtn {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
  opacity: 0;
}

.fadein {
  animation: fadein 400ms forwards;
}

.fadeout {
  animation: fadeout 400ms;
}

@keyframes fadein {
  to {
    opacity: 1;
  }
}

@keyframes fadeout {
  from { opacity: inherit;}
  to {
    opacity: 0;
  }
}
</style>



